@import "../../../css/module/fn";
@import "../../../css/module/color";

.wrap {
	margin: p2m(40);
	border-radius: p2m(15);
	overflow: hidden;
	@include shadow;
	.resultBtnSelect {
		display: none;
	}
	
	#btnSelect {
		background-color: $red;
		color: white;
		width: 100%;
		height: p2m(110);
	}
	table {
		width: 100%;
		background-color: #fff;
		table-layout: fixed;
		
		&.list {
			tr:nth-of-type(odd) {
				background-color: #eee;
			}
			td{
				@include textClip();
				
				.ico-handle{
					float: right;
					font-size: 22px;
					color: #ccc;
					line-height:2;
				}
			}
		}
		tr {
			height: p2m(85);
			line-height: p2m(85);
			th {
				font-weight: 900;
			}
			td, th {
				font-size: p2m(24);
				text-align: center;

		
			}
		}
	}
	
}

#divBusyDateList {
	width: p2m(700);
	border-radius: 0.10667rem;
	padding: 0.66667rem 0.33333rem;
	background-color: #fff;
	box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
	margin: p2m(44) auto 0;
	.switchDate.mui-switch {
		width: p2m(270);
	}
	.switchDate.mui-switch.mui-active .mui-switch-handle {
		-webkit-transform: translate(p2m(204), 0);
		transform: translate(p2m(204), 0);
	}
	.switchDate-en.mui-switch.mui-active:before {
		content: 'unavailable';
	}
	.switchDate-en.mui-switch:before {
		content: 'available';
	}
	.switchDate-cn.mui-switch.mui-active:before {
		content: '不可用';
	}
	.switchDate-cn.mui-switch:before {
		content: '可用';
	}
	
}

.deleteSpan {
	color: red;
}